import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { LibraryComponent } from './library/library.component';
import { ConferencesComponent } from './conferences/conferences.component';
import { MembershipComponent } from './membership/membership.component';
import { SignUpComponent } from './membership/sign-up/sign-up.component';
import { SignInComponent } from './membership/sign-in/sign-in.component';
import { TeamComponent } from './team/team.component';
import { AuthGuard } from './auth/authGuard/auth.guard';
import { GalleryComponent } from './mormo-images/gallery/gallery.component';
import { AboutusComponent } from './aboutus/aboutus.component';


const routes: Routes = [
  { path: '', component: HomeComponent, pathMatch: 'full' },
  { path: 'home', component: HomeComponent},
  { path: 'library', component: LibraryComponent},
  { path: 'mormousers', redirectTo:'/login', pathMatch : 'full'},
  { path: 'conferences', component: ConferencesComponent},
  { path: 'aboutus', component: AboutusComponent},
  { path: 'images', component: GalleryComponent },
  { path: 'images/:category', component: GalleryComponent },
  { path: 'contactus', component: ContactUsComponent},
  { path: 'team', component: TeamComponent},
  { path: 'admin', redirectTo: 'admin/home', canActivate:[AuthGuard] },
  {
    path: 'signup', component: MembershipComponent,
    children: [{ path: 'signup', component: SignUpComponent }]
  },
  {
      path: 'login', component: MembershipComponent,
      children: [{ path: 'login', component: SignInComponent }]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
