import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { CounterComponent } from './counter/counter.component';
import { FetchDataComponent } from './fetch-data/fetch-data.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule } from './material/material.module';
import { AppRoutingModule } from './app-routing.module';
import { HeaderComponent } from './navigation/header/header.component';
import { LayoutComponent } from './layout/layout.component';
import { FlexLayoutModule } from '@angular/flex-layout';
import { SidenavListComponent } from './navigation/sidenav-list/sidenav-list.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { LibraryComponent } from './library/library.component';
import { ConferencesComponent } from './conferences/conferences.component';
import { MormobannerComponent } from './mormobanner/mormobanner.component';
import { FooterComponent } from './footer/footer.component';
import { MembershipComponent } from './membership/membership.component';
import { SignInComponent } from './membership/sign-in/sign-in.component';
import { SignUpComponent } from './membership/sign-up/sign-up.component';
import { UserService } from './shared/services/user-service/user.service';
import { ToastrModule } from 'ngx-toastr';
import { TeamComponent } from './team/team.component';
import { AuthInterceptor } from './auth/interceptor/auth.interceptor';
import { ImageService } from './shared/services/image-service/image.service';
import { TeamService } from './shared/services/team-service/team.service';
import { ImageListComponent } from './mormo-images/image-list/image-list.component';
import { ImageNavigationComponent } from './mormo-images/image-navigation/image-navigation.component';
import { GalleryComponent } from './mormo-images/gallery/gallery.component';
import { LayoutModule } from './admin/layout/layout.module';
import { AdministrationModule } from './admin/administration/administration.module';
import { AboutusComponent } from './aboutus/aboutus.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    CounterComponent,
    FetchDataComponent,
    HeaderComponent,
    LayoutComponent,
    SidenavListComponent,
    ContactUsComponent,
    LibraryComponent,
    GalleryComponent,
    ConferencesComponent,
    MormobannerComponent,
    FooterComponent,
    MembershipComponent,
    SignInComponent,
    SignUpComponent,
    TeamComponent,
    ImageListComponent, 
    ImageNavigationComponent, AboutusComponent
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule, MaterialModule, LayoutModule, AdministrationModule,
    FormsModule, ReactiveFormsModule, AppRoutingModule, FlexLayoutModule,
    BrowserAnimationsModule, ToastrModule.forRoot({
      progressBar:true
    })
  ],
  providers: [ImageService,TeamService,  UserService, {
                                        provide: HTTP_INTERCEPTORS,
                                        useClass: AuthInterceptor,
                                        multi: true
                                      }
],
  bootstrap: [AppComponent]
})
export class AppModule { }
